@import '../base/typography.scss';
@import '../abstracts/variables.scss';

::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    background: $ARCTICLIME;
};

::-webkit-scrollbar-track {
    background: $DUSK;
};

::-webkit-scrollbar-thumb {
    background: $ARCTICLIME;
};

::-webkit-scrollbar-thumb:hover {
    background: $COTTONCANDY;
};

.white {
    background-color: $SMOKEWHITE;
    scrollbar-color: $ARCTICLIME $SMOKEWHITE;
};


.servicegrid {
    display: grid;
    width: 85vw;
    height: 100vh;
    margin-left: 7.5vw;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    margin-top: 5.5vw;
    margin-bottom: 4vw;
    padding: 20px;
    background-color: $SMOKEWHITE;
};

.servicecontent {
    border: 2px solid black;
    border-radius: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    padding-left: 0.7vw;
    padding-bottom: 0.5vw;
};

.context {
    font-family: $BG;
    text-transform: uppercase;
    font-size: 1.1vw;
    color: $SMOKEWHITE; 
    -webkit-user-select: none; 
       -moz-user-select: none; 
        -ms-user-select: none; 
            user-select: none;
    cursor: pointer;
};

.editing, .mograph, .audio, .animation, .subs, .delivery, .authoring, .duplication, .uxui, .webdev, .upscale, .translation {
    -o-object-fit: cover;
       object-fit: cover;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    max-width: 800px;
    min-width: 300px;  
};

.audio, .animation, .subs {
    background-position-y:bottom !important;
}

.authoring {
    background-position-y:top !important;
};

.modalcontainer {
    width: 99vw;
    height: 60vh;
    background-color: $DUSK;
    opacity: 1;
    display: flex;
    align-items: center;
    flex-flow: column nowrap;
    justify-content: center; 
};

.modalheader {
    font-family: $BG;
    color: $DUSK;
    background-color: $SMOKEWHITE;
    font-size: 2vw;
    margin-bottom: 0.5vw;
    padding-top: 0.3vw;
    padding-left: 0.2vw;
    padding-right: 0.2vw;
    margin-top: 2vw;
};

.modaldesc {
    font-family: $CS-ME;
    color: $SMOKEWHITE;
    font-size: 1.1vw;
    letter-spacing: 2px;
    margin-top: 1vw;
    margin-bottom: 1vw;

};

.modaltxt {
    width: 40%;
    font-family: $CS-RE;
    color: $SMOKEWHITE;
    font-size: 0.9vw;
    text-align: justify;
    margin-top: 1vw;
    margin-bottom: 8.5vw;
};

.modalclose {
    font-family: $CS-LI;
    color: $SMOKEWHITE;
    font-size: 0.5vw;
    letter-spacing: 5px;
    text-transform: uppercase;    
};

@media screen and (min-width: 1px) and (max-width: 669px) and (orientation: portrait) {
    .servicegrid {
        grid-template-columns: 1fr;
        grid-template-rows: repeat(12, 5fr);
        padding: 0px;
        margin-top: 7.6vh;
        margin-bottom: 0.7vh;
        margin-left: 0;
        width: 100%;
        height: 100%;
        grid-row-gap:5px;
        grid-column-gap: 0px;
    };

    .servicecontent {
        border-radius: unset;
        justify-content: center;
        align-items: center;
        height: 15vh;
    };

    .context {
        font-size: 1.3vh;
    };

    .audio {
        background-position-y: top !important;
    };

    .animation {
        background-position-y: center !important;
        background-position-x: left !important
    };

    .upscale {
        background-position-x: left !important;
        background-position-y: center !important;
    };

    .modalcontainer {
        height: unset;
    }

    .modalheader {
        font-size: 2vh;
        margin-bottom: 0.5vh;
        padding-top: 0.3vh;
        padding-left: 0.2vh;
        padding-right: 0.2vh;
        margin-top: 5vh;
    };
    
    .modaldesc {
        font-size: 1.2vh;
        margin-top: 1vh;
        margin-bottom: 1vh;
        width: 65%;
        text-align: center;
        letter-spacing: 1px !important;
    };
    
    .modaltxt {
        font-size: 1.2vh;
        margin-top: 1vh;
        margin-bottom: 6vh;
        line-height: 1.8vh;
    };

    .modalclose {
        font-size: 0.7vh !important;
        letter-spacing: 0.5px !important;
        margin-bottom: 2vh;
    };
};

@media screen and (min-width:670px) and (orientation: portrait) {
    .servicegrid {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(6, 1fr);
        margin-top: 7vh;
        margin-bottom: 3.5vh;
        margin-left: unset;
        width: 100vw;
      
    };

    .servicecontent {
        border-radius:5px;
        grid-row-gap: 20px;
        grid-column-gap: 20px;
        justify-content: flex-start;
        align-items: flex-end;
        padding-left: 0.5vh;
        padding-bottom: 0.5vh;
    };

    .context {
        font-size: 1.1vh;
    };
    
    .mograph {
        background-position-y: center;
        background-position-x: left;
    };

    .audio {
        background-position-y: unset !important;
    };

    .animation {
        background-position-y: unset !important;
    };

    .authoring {
        background-position-y: top !important;
        background-position-x: right !important;
    };

    .delivery {
        background-position-y: top;
    };

    .uxui {
        background-position-y: center;
        background-position-x: left;
    };
    
    .modalcontainer {
        height: unset;
    }
    
    .modalheader {
        font-size: 2.8vh;
        margin-bottom: 0.5vh;
        padding-top: 0.3vh;
        padding-left: 0.2vh;
        padding-right: 0.2vh;
        margin-top: 5vh;
    };
    
    .modaldesc {
        font-size: 1.5vh;
        margin-top: 1vh;
        margin-bottom: 1vh;
    };
    
    .modaltxt {
        font-size: 1.3vh;
        margin-top: 1vh;
        margin-bottom: 6vh;
        line-height: 2vh;
    };
    
    .modalclose {
        font-size: 0.7vh !important;
        letter-spacing: 0.5px !important;
        margin-bottom: 2vh;
    };
};
